import { DEFAULT_AVATAR } from './../../../services/constants';
import { FriendGroups, Friends } from './../../../models/contact';
import { SqliteProvider } from './../../../providers/sqlite-provider';
import { Session } from './../../../services/session';
import { StorageService } from './../../../services/storage-service';
import { PinyinService } from './../../../services/pinyin';
import { ContactProvider } from './../../../providers/contact-provider';
import { Component, AfterViewInit, ViewChild, ChangeDetectorRef,  ElementRef } from '@angular/core';
import { IonicPage, NavController, Events, ModalController, PopoverController, Content, Popover } from 'ionic-angular';
import { PreloadProvider } from './../../../providers/preload-provider';
 

@IonicPage({ name: 'ContactPage' })

@Component({
  selector: 'page-contact',
  templateUrl: 'contact.html'
})
export class ContactPage implements AfterViewInit {
 
  allItems: any; //最终数组
  searchText: string; //搜索key
  searchItems: any; //搜索结果
  isFilter = false; //是否为搜索 
  latestFriends: any; //最新好友请求
  newFriendsRequest: number = 0; //最新好友请求数量 
  friendTags: any[] = []; //好友标签
  popover: Popover; //更多选项popover
  allGroups: any;//從sqlite表里獲取的所有分組
  allFriends: any;//從sqlite表里獲取的所有好友
  storageKey: string = '';//是否已经从api加载过数据key
  chosenTag:any; //选中好友标签
  showlist:any //显示数组
  showAllFriends: boolean=true;//是否显示全部好友
  isShowOptions: boolean = false; //更多选项标识
  _currentIndicator ; //当前alpha
  _flag = true;
  _indexes: any[] = []; // 右侧导航
  _offsetTops: Array<number> = []; // 每个AlphaSection 的offsetTop
  _navOffsetX: 0;
  _indicatorTime: any = null;
  _showModal = false;
  _alphaClassName = 'alpha-header';
  _canShowModal = false;
  @ViewChild(Content) content: Content; 
  @ViewChild('list') list: ElementRef ; 
   constructor(public navCtrl: NavController,
    public contactProvider: ContactProvider,
    public modal: ModalController,
    public popoverCtrl: PopoverController,
    public preload: PreloadProvider,
    public events: Events,
    public sqliteProvider: SqliteProvider,
    public storageService: StorageService,
    public pinyin:PinyinService,
    public session: Session,
    public cd:ChangeDetectorRef ) { 

    this.storageKey = `contact-${this.session.userId}:isLoadFriends`;
    this.getMyFriends();
    this.LatestFriendRequest();

    
  } 

  ionViewDidLoad(){
    this.events.subscribe('contact:refresh-myFriends', () => { 
      this.AfterGetFriendsFromApi(this.contactProvider.friends);
    }) 
    
    this.events.subscribe('contact:refresh-newFriends', () => {
      this.LatestFriendRequest();
    }) 

    this.events.subscribe('contact: refresh-friendsFromSqlite',()=>{
      this.getMyFriends();
    })
    setTimeout(()=>{
      this._canShowModal = true;
    },1000)
  }

  ionViewWillUnload() {
    //console.log('界面销毁');
    this.events.unsubscribe('contact:refresh-myFriends');
    this.events.unsubscribe('contact:refresh-newFriends');
    this.events.unsubscribe('contact: refresh-friendsFromSqlite');
    this._canShowModal = false;
  }

  /**
   * 横向滚动条宽度
   */
  scrollWidth(){   
    return {
      "width": (window.screen.availWidth - 5)   + 'px',
      "height": 44 + 'px',
    }
  }
  
  ngAfterViewInit() {
  }

  ionViewWillEnter() {
    this.popover && this.popover.dismiss();
    this.isShowOptions = false; 
    
  }
 
  /**
   * 刷新
   * @param _event 
   */
  doRefresh(_event){
    this.getMyFriends(true)
    this.LatestFriendRequest();
    setTimeout(()=>{
      _event.complete()
    },1000)
  }
 

  /**打開添加好友頁面 */
  openContactAddPage() {
    this.navCtrl.push('ContactAddPage', { latestFriends: this.latestFriends });
  }
 
  /**
   * 获取全部好友
   */
  getAllFriends(){
    this.showAllFriends = true; 
    for( let tag of this.friendTags){
      tag.isSelected = false; 
    }
    this.allItems = this.createContactList(this.allFriends); 
  }

  /**
   * 根据标签获取好友
   * @param tag 
   */
  getFriendsByTag(tag){
    this.showAllFriends=false;
    tag.isSelected=true; 
    this.showlist = [];
    this.allFriends.forEach(friend => {
      if(tag.friendTagId == friend.friendTagId)this.showlist.push(friend)
    });
    for (let item of this.friendTags) {
      if (item.friendTagId == tag.friendTagId) {
        item.isSelected = true
      } else {
        item.isSelected = false
      }
    }
    this.allItems = this.createContactList(this.showlist);
  }

  /**
   * 創建通訊錄列，適應排序組件
   * @param friends 當前應該要顯示的好友數組
   */
  createContactList(friends) {
    let starStr = '\u2606'; //星標好友顯示標識
    let otherStr = '#'; //非字母顯示標識
    let starItem = {    
      id: starStr,
      items: []
    }
    let res = []
    let p = /[A-Z]/i;
    for (let item of friends) { 
      if((item.IsStar || item.isStar) && !item.isBlack){
        starItem.items.push(item)
      } else if(!item.isBlack){
        let name = item.aliasName || item.name; 
        if (name && p.test(this.pinyin.getCamelChars(name).substr(0, 1).toUpperCase())) {
           const firstAlpha = this.pinyin.getCamelChars(name).substr(0, 1).toUpperCase();
           const currentAlphaIndex = res.findIndex(p => p.id == firstAlpha);
           if (currentAlphaIndex > -1) { 
             res[currentAlphaIndex].items.push(item)
           } else {
             res.push({ id: firstAlpha, items: [item] })
           }
        } else {
           const currentOtherIndex = res.findIndex(p => p.id == otherStr); 
           if(currentOtherIndex > -1){ 
             res[currentOtherIndex].items.push(item);
           } else{
             res.push({ id: otherStr, items: [item] })
           }
        }
      }  
    }
 
    res.sort((a, b) => {
      if (a.id > b.id) {
        return 1;
     } else {
        return -1;
     }
    })
    if(starItem.items.length > 0) {
      res.unshift(starItem)
    }
    this.cd.detectChanges() 
    this.content.resize()
    return res;
  }

  /**
   * 獲取我的好友，已經從api獲取過好友並且不是刷新，則從SQLITE中加載好友列表，否則，從API中獲取數據
   * @param isRefresh 是否為刷新
   */
  async getMyFriends(isRefresh: boolean = false) {
    
    let isLoadFriends = await this.storageService.getItem(this.storageKey)
    this.allFriends = [] 
    this.friendTags = []
    if (isLoadFriends && !isRefresh) {
      this.sqliteProvider.queryFriendsGroups().subscribe(groups=>{
        // query db     
        this.sqliteProvider.queryContacts().subscribe((friends) => { 
          friends.forEach(element=>{
            let extra = JSON.parse(element.Extra)
            if(!extra.isBlack){
              this.allFriends.push(extra)
              if(extra.friendTagId != -1 && extra.friendTagId != 0 && this.friendTags.findIndex(p=>p.friendTagId == extra.friendTagId) == -1){ 
                this.friendTags.push({friendTagId: extra.friendTagId,friendTagName: extra.friendTagName})
              }
            }
          })  
          groups.forEach(group=>{ 
            for(let tag of this.friendTags){
              if(tag.friendTagId == group.GroupId){
                tag.friendTagName = group.GroupName;
              }
            }
          })
          this.getAllFriends();
      })
      
      });     
    } else {
      this.contactProvider.MyFriends().subscribe();     
    }
  }

 
  /**
   * 從api獲取到好友之後的數據處理
   * @param result api結果集
   */
  AfterGetFriendsFromApi(result){
    
    let allFriends = result ; 
    this.friendTags = [];  
    let saveFriends = []; 
    this.allFriends = [];
    for(let friend of  allFriends){ 
      friend.avator = friend.avator || DEFAULT_AVATAR; 

      let friendItem: Friends = {
        GroupId: friend.friendTagId,
        FriendId: friend.id,
        IsStar:friend.IsStar || friend.isStar,
        Extra: friend
      } 
      saveFriends.push(friendItem) 
      if(!friend.isBlack && friend.friendTagId != -1 && friend.friendTagId != 0 && this.friendTags.findIndex(p=>p.friendTagId == friend.friendTagId) == -1){ 
        this.friendTags.push({friendTagId: friend.friendTagId,friendTagName: friend.friendTagName})
      } 
      if(!friend.isBlack)this.allFriends.push(friend) 
    }
 
    this.sqliteProvider.saveContacts(saveFriends).subscribe() ;
    this.storageService.setItem(this.storageKey, true);
    
    this.contactProvider.FriendTagList().subscribe(res=>{  
      if(res.result){
        let saveGroups = []
        res.result.forEach(element => {
          let Group = {
            GroupId: element.id,
            GroupName: element.name
          }
          saveGroups.push(Group)
          for(let tag of this.friendTags){
            if(tag.friendTagId ==  element.id){
              tag.friendTagName =  element.name;
            }
          };  
        });
        this.friendTags.sort((a,b)=>{
          return a.friendTagId - b.friendTagId
        })
        this.content.resize()
        this.sqliteProvider.saveFriendsGroup(saveGroups).subscribe(); //  保存/更新分组
      } 
    })
    this.getAllFriends();
  }

  /**
   * 打開好友資料
   * @param contactId 
   */
  openContactDetails(contactId: string) {
    this.navCtrl.push('ContactDetailPage', { friendId: contactId, fromPage: 'friendsList' });
  }

  /**
   * 點擊頭像放大
   * @param contact 
   */
  viewAvatar(contact) {
    this.preload.previewImage(contact.avatar, 0);
  }

  /**
   * 最近好友請求
   */
  LatestFriendRequest() {
    this.contactProvider.LatestFriendRequest().subscribe(res => {
      this.newFriendsRequest = 0;
      this.latestFriends = res.result;
      for (let item of this.latestFriends) {
        if (item.status == 'waiting') {
          this.newFriendsRequest++;
        }
      }
    })
  }


  /**
   * 查找好友
   * @param ev 
   */
  getCurrentItems(ev: any) {
    this.searchItems = [];
    if (!this.searchText) {
      this.isFilter = false;
      this.content.resize();
      return;
    }
    this.isFilter = true;
    this.content.resize();
    //搜索的時候默認全部好友分組 

    let val = ev.target.value;
    if (val && val.trim() != '') {

      for(let friend of this.allFriends){
        if (this.filterName(val.trim(), friend.aliasName) || this.filterName(val.trim(), friend.name) && !friend.isBlack) {
          this.canInsertSearchItem(friend.id) && this.searchItems.push({
            // source: source,
            friendTagId: friend.friendTagId,
            displayName: friend.aliasName ? friend.aliasName + '(' + friend.name + ')' : friend.name,
            friendId: friend.id,
            avator: friend.avator
          });
        }
      }
      // for (let group of this.friendTags) {
      //   let source = group.friendTagName;
      //   for (let item of group.friends) {
      //     if (this.filterName(val.trim(), item.aliasName) || this.filterName(val.trim(), item.name) && !item.isBlack) {
      //       this.canInsertSearchItem(item.id) && this.searchItems.push({
      //         source: source,
      //         friendTagId: group.friendTagId,
      //         displayName: item.aliasName ? item.aliasName + '(' + item.name + ')' : item.name,
      //         friendId: item.id,
      //         avator: item.avator
      //       });
      //     }
      //   }
      // }
    }
  }

  canInsertSearchItem(friendId: string): boolean {
    let index = this.searchItems.findIndex(p => p.friendId == friendId)
    if (index > -1 && this.searchItems[index].friendTagId == -1) {
      return false
    } else {
      return true
    }
  }

  onClearFilter(ev: any) {
    this.isFilter = false;
  }
  
  /**
   * 
   * @param searchValue 搜索名稱
   * @param itemName 源名稱
   */
  filterName(searchValue: string, itemName: string){
    let LoweritemName = itemName.toLowerCase();
    let LowersearchValue = searchValue.toLowerCase();
    let CamelChars = this.pinyin.getCamelChars(itemName)
    let FullChars = this.pinyin.getFullChars(itemName)
    if(LoweritemName.indexOf(LowersearchValue) > -1 
    || CamelChars.toLowerCase().indexOf(LowersearchValue) > -1 
    || FullChars.toLowerCase().indexOf(LowersearchValue) > -1 ){
      return true;
    }
 
    for(let item of itemName.split('')){
      let fullchars = this.pinyin.getFullChars(item);
      if(fullchars.toLowerCase() == LowersearchValue){
        return true;
      }
    }
    return false;
  }

  /**
   * 打開更多選項
   * @param myEvent 
   */
  openContactAddModal(myEvent) {
    this.isShowOptions = true;
    let cb = {
      openContactAddPage: () => {
        this.navCtrl.push('ContactAddPage', { latestFriends: this.latestFriends });
      },
      openContactGroupManage: () => {
        this.navCtrl.push('ContactGroupManagePage');
      },
      openBlackListManage: () => {
        this.navCtrl.push('BlackListPage');
      },
    };
    this.popover = this.popoverCtrl.create('ContactMoreModalPage', {
      callback: cb
    });
    this.popover.present({
      ev: myEvent
    }); 

    this.popover.onWillDismiss(()=>{
      this.isShowOptions = false;
    }) 
  } 

  /**  */
  ngAfterViewChecked(): void {
    this._createListOfAlphaSection();
    this.cd.detectChanges();  
 }

 _createListOfAlphaSection(){ 
   // this._flag = true
   // this._indexes = []
   // this._offsetTops = [] 
   if (this._flag && this.list && this.list.nativeElement && this.list.nativeElement.children.length > 0) { 
     let collection = this.list.nativeElement.children;
     for(let section of collection){
       if(this._alphaClassName == section.className){
        this._indexes.push(section.id);
        const offsetTop = section.offsetTop;
        this._offsetTops.push(offsetTop);
       } 
     }
     this._currentIndicator = this._indexes[0];
     if(collection.length > 0){
       this._flag = false; 
     }   
   }
 }
  scrollEvent(e: any) { 
    const scrollTopOffsetTop = this.content.scrollTop; 
    this._offsetTops.forEach((v, i) => {
      if (scrollTopOffsetTop >= v) { 
        if(this._offsetTops[i + 1]){
          if(scrollTopOffsetTop <= this._offsetTops[i + 1])this._currentIndicator = this._indexes[i];
        }else{
          this._currentIndicator = this._indexes[i];
        }
         
        this.setCurrentSection(this._currentIndicator);
      }

    });
  }

  scrollList(y: any) { 
    const currentItem: any = document.elementFromPoint(this._navOffsetX, y);
    if (!currentItem || !currentItem.classList.contains('alpha-bar')) {
      return;
    }
    this._currentIndicator = currentItem['innerText'];
    const index = this._indexes.indexOf(this._currentIndicator);
 
    this.content.scrollTop = this._offsetTops[index];

    this._showModal = true;
    if (this._indicatorTime) {
      clearTimeout(this._indicatorTime);
    }
  }

  touchstart(e: any) {
    this._navOffsetX = e.changedTouches[0].clientX;
    this.scrollList(e.changedTouches[0].clientY);
  }

  touchmove(e: any) {
    e.preventDefault();
    this.scrollList(e.changedTouches[0].clientY);
  }

  touchend(e: any) {
    this._indicatorTime = setTimeout(() => {
      this._showModal = false;
      // this._currentIndicator = '';
    }, 500);
  }

  setCurrentSection(currentindex: string) { 
    if (this.list && this.list.nativeElement && this.list.nativeElement.children.length > 0) { 
      let collection = this.list.nativeElement.children;
      for(let section of collection){
        if (section.id === currentindex) {
          section._current = true;
        }else {
          section._current = false;
        }
      }
 
      }   
    } 
 
}



